import { MrdConfig } from "../model/config.model";
import * as _ from 'underscore';

export class ConfigUtil {

  private static config: MrdConfig;
  private static customConfig: MrdConfig;

  public static setConfig(config: MrdConfig) {
    this.customConfig = config;
  }

  static getConfig() {
    if (this.config) {
      return this.config;
    }
    // let url = 'assets/mrd-core-ui.config.js';
    // let config: MrdConfig = {} as MrdConfig;
    // let xhr = new XMLHttpRequest();
    // xhr.open('GET', url, false);
    // xhr.onreadystatechange = () => {
    //   if (xhr.readyState === 4 && xhr.status === 200) {
    //     config = this.parseJsObject(
    //       xhr.responseText.substring(xhr.responseText.indexOf('module.exports'))
    //       .replace('module.exports =', '')
    //       .replace('module.exports=', '')
    //       .trim()
    //     );
    //   }
    // };
    // xhr.send();
    // let config = loadConfig();
    // this.config = config;
    // return config;

    let config = this.customConfig;
    let defaultConfig = this.baseConfig;

    this.extendObject(defaultConfig, config);

    this.config = defaultConfig;
    console.log(this.config);
    return defaultConfig;
  }

  // private static parseJsObject(data: string): any {
  //   const func = new Function('return ' + data + ';');
  //   return func();
  // }

  private static extendObject(obj: any, extObj: any): any {
    for (const [key, value] of Object.entries(extObj)) {
      if (_.isObject(value) && !_.isArray(value)) {
        obj[key] = this.extendObject(obj[key], value);
      } else {
        obj[key] = value;
      }
    };
    return obj;
  }

  private static get baseConfig(): MrdConfig {
    return {
      baseColors: {
        primary: "#68b022",
        accent: "#e7e7e7",
        warn: "#b00122",
        disabled: "#afa6a6"
      },
      button: {
        backgroundColor: "transparent",
        textLightColor: "#ffffff",
        textDarkColor: "#000000",
        hoverColor: "#d3d3d361",
        activeColor: "#d3d3d3",
        disabled: {
          text: "#a6a6a6",
          background: "transparent"
        },

        border: "0 unset unset",
        borderRadius: "4px",
        outline: {
          border: "1px solid #d3d3d3"
        },

        flat: {
          backgroundColor: "#ffffff",
          disabled: {
            text: "#a6a6a6",
            background: "#d3d3d3"
          }
        },
        raised: {
          backgroundColor: "#ffffff",
          disabled: {
            text: "#a6a6a6",
            background: "#d3d3d3"
          }
        },
        icon: {
          borderRadius: "50%"
        },

        fab: {
          backgroundColor: "#ffffff",
          disabled: {
            text: "#a6a6a6",
            background: "#d3d3d3"
          },
          borderRadius: "50%"
        },
        miniFab: {
          backgroundColor: "#ffffff",
          disabled: {
            text: "#a6a6a6",
            background: "#d3d3d3"
          },
          borderRadius: "50%"
        }
      }
    }
  }
}
